import { useContext, useEffect, useState } from "react";
import PropTypes from 'prop-types';

import { DeleteProduct, GetProducts } from "../services/products";
import Table from "./Table";
import { ROUTES } from "../constants";

import { AppContext } from "../context/applicationProvider";

const TableProducts = ({ onSelect, readOnly }) => {
    const [listProducts, setListProducts] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        setListProducts(await GetProducts(filter))
        appContext.setIsLoading(false)
    }
    const HandleOnDelete = (item) => {
        appContext.setIsLoading(true)
        DeleteProduct(item.id)
        appContext.setIsLoading(false)
    }
    const onchange = async (value) => {
        if (value.length === 0) {
            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["Codigo", "Nombre del Producto", "price sales", "price purchase", "image"]
    const indexColumnsHidden = [5]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.PRODUCTS_NEW,
        linkToEdit: ROUTES.PRODUCTS_EDIT
    }
    return (<>

        <div className="w-full">
            <Table indexColumnsHidden={ indexColumnsHidden } datas={ listProducts } columns={ columns } onChangeSearch={ onchange } actions={ actions } />
        </div>

    </>

    )
}
TableProducts.protoTypes = {
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TableProducts.defaultProps = {
    onSelect: null,
    readOnly: false
}
export default TableProducts