import { useContext, useEffect, useState } from "react";
import PropTypes from 'prop-types';
import { ROUTES } from "../constants"
import { DeleteProvider, GetProviders } from "../services/providers";
import Table from "./Table";

import { AppContext } from "../context/applicationProvider";

const TableProviders = ({ onSelect, readOnly }) => {
    const [listProviders, setListProviders] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        setListProviders(await GetProviders(filter))
        appContext.setIsLoading(false)
    }
    const HandleOnDelete = async (supplier) => {
        await DeleteProvider(supplier.id)
    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    const HandleOnselect = (item) => {
        onSelect(item)
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["Nombre", "Apellido", "email", "Telefono"]
    const indexColumnsHidden = [0, 5]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.PROVIDERS_NEW,
        linkToEdit: ROUTES.PROVIDERS_EDIT
    }

    return (<>

        <div className="w-full">
            < Table indexColumnsHidden={ indexColumnsHidden } datas={ listProviders } columns={ columns } onChangeSearch={ onchange } actions={ actions } onSelect={ HandleOnselect } />
        </div>
    </>
    )
}
TableProviders.protoTypes = {
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TableProviders.defaultProps = {
    onSelect: (item) => {},
    readOnly: false
}


export default TableProviders