
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js";
const URL_SERVER_CLIENTS = URL_SERVER + "/clients"

export const GetClients = async (filter) => {
    try {

        let url = URL_SERVER_CLIENTS
        if (filter !== '') {
            const params = `?firstName=${ filter }&&lastName=${ filter }&&email=${ filter }&&phone=${ filter }`
            url = URL_SERVER_CLIENTS + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {

        handlerError(error); console.log(error)

    }
}
export const GetClientById = async (id = 0) => {
    try {
        let url = URL_SERVER_CLIENTS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateClient = async ({ id, firstName, lastName, email, phone }) => {
    try {
        let url = URL_SERVER_CLIENTS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { firstName, lastName, email, phone })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreateClient = async ({ firstName, lastName, email, phone }) => {
    try {
        let url = URL_SERVER_CLIENTS
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { firstName, lastName, email, phone })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeleteClient = async (id) => {
    try {
        let url = URL_SERVER_CLIENTS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}