import { useContext, useEffect } from "react";
import useInput from "../hook/useInput";
import { CreateProduct, GetProductById, UpdateProduct } from "../services/products";
import TextField from "../components/TextField";
import { IconDolar, IconText } from "../components/Icons";
import Button from "../components/Button";
import { useNavigate } from "react-router-dom";

import { AppContext } from "../context/applicationProvider";

/*********************FORM *********************/
const FormProduct = ({ productId }) => {
    const id = useInput("", 'id')
    const description = useInput("", 'Description')
    const priceSales = useInput("", 'PriceSales')
    const pricePurchase = useInput("", 'PricePurchase')
    let appContext = useContext(AppContext)
    const navegate = useNavigate()

    const HandleOnSubmitForm = (env) => {
        appContext.setIsLoading(true)
        if (productId) {
            UpdateProduct({ id: id.props.value, description: description.props.value, priceSales: priceSales.props.value, pricePurchase: pricePurchase.props.value })

        } else {
            CreateProduct({ id: id.props.value, description: description.props.value, priceSales: priceSales.props.value, pricePurchase: pricePurchase.props.value })
        }
        appContext.setIsLoading(false)
        navegate("/")
    }
    async function getData (productId) {
        appContext.setIsLoading(true)
        const prod = await GetProductById(productId)
        id.setValue(prod.id)
        description.setValue(prod.description)
        priceSales.setValue(prod.priceSales)
        pricePurchase.setValue(prod.pricePurchase)
        appContext.setIsLoading(false)
    }

    useEffect(() => {
        if (productId) {
            getData(productId)
        }
    }, [productId])
    return (<>
        <div >
            <h1 className="mb-5">Formulario Productos</h1>
            <div className='center border-2 border-color-segundary'  >
                <TextField
                    { ...description.props }
                    name="description"
                >  <IconText /></TextField>
                <TextField
                    { ...priceSales.props }
                    name="priceSales"
                >  <IconDolar /></TextField>
                <TextField
                    { ...pricePurchase.props }
                    name="pricePurchase"
                >  <IconDolar /></TextField>
            </div>
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button primary disabled={ description.props.error || priceSales.props.error || pricePurchase.props.error }
                    onClick={ HandleOnSubmitForm }>Guardar</Button>
            </div>
        </div >

    </>
    )
}
export default FormProduct