import { useEffect,  useState } from "react";
import PropTypes from 'prop-types';
const TextField = ({
    name,
    value,
    onchange,
    onBlur,
    label,
    error,
    helperText, type = "Text",
    className
}) => {
    const [currrentvalue, setCurrrentvalue] = useState(value)

    const handleOnChange = (env) => {
        onchange(env.target.value)
        setCurrrentvalue(env.target.value)
    }

    const handleOnBur = () => {
        onBlur()
    }

    useEffect(() => {
        setCurrrentvalue(value)
    }, [value])
    const classError = "text-red-700 focus:ring-red-500 focus:border-red-500 focus:ring-red-500 border-red-500";
    const classSuccess = "text-gray-900 focus:ring-blue-500 focus:border-blue-500";
    const classState = error ? classError : classSuccess;

    const renderInput = () => {
        switch (type) {

            case "password": {
                return (<input

                    type="Password"
                    name={ name }
                    className={ " border  text-sm rounded-lg  block w-full p-2.5 " + classState }
                    placeholder={ helperText }
                    onBlur={ handleOnBur }
                    onChange={ handleOnChange }

                />)
            }
            case "number": {
                return (<input

                    type="Number"
                    name={ name }
                    className={ " border  text-sm rounded-lg  block w-full p-2.5 rig" + classState }
                    placeholder={ helperText }
                    onBlur={ handleOnBur }
                    onChange={ handleOnChange }
                    value={ currrentvalue }
                />)
            }
            default: {

                return (<input

                    type="text"
                    name={ name }
                    className={ " border  text-sm rounded-lg  block w-full  p-2.5 " + classState }
                    placeholder={ helperText }
                    onBlur={ handleOnBur }
                    onChange={ handleOnChange }
                    value={ currrentvalue }
                />)
            }
        }
    }
    useEffect(() => {

        setCurrrentvalue(value)
    }, [value])

    return (
        < div className={ className } >
            <label
                className={ "block mb-2 text-sm " + classState }
            >
                { label }
            </label>
            <div className="relative font-medium ">
                { renderInput() }
            </div>

        </div>
    );
}
TextField.protoTypes = {
    name: PropTypes.string,
    value: PropTypes.string,
    onchange: PropTypes.func,
    onBlur: PropTypes.func,
    label: PropTypes.string,
    error: PropTypes.string,
    helperText: PropTypes.string
}
TextField.defaultProps = {
    name: "",
    value: "",
    onchange: () => {},
    onBlur: () => {},
    label: "",
    error: "",
    helperText: ""
}
export default TextField
