
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_SALES = URL_SERVER + "/payments"

export const GetPaymentBySalesId = async (noteSalesId) => {
    try {
        let url = URL_SERVER_SALES + "/BySalesId/" + noteSalesId
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const addPayment = async (noteSalesId, mount) => {
    try {
        let url = URL_SERVER_SALES
        const response = await axios.post(url, { noteSalesId, mount })
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeletePayment = async (paymentId) => {
    try {
        let url = URL_SERVER_SALES + "/" + paymentId
        const response = await axios.post(url)
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}