
import IconButton from "./IconButton";
import { IconSearch } from "./Icons";

export default function FieldSearch ({ label = "", onchange = (e) => {}, onKeyEnter = (e) => {}, onClickIconButton = () => {} }) {
    const HandleChange = (e) => {
        onchange(e.target.value)

    }
    const HandleKeyEnter = (e) => {
        if (e.keyCode === 13) {
            e.preventDefault();
            onKeyEnter(e.target.value)
        }
    }
    const HandleClickIcon = () => {
        onClickIconButton()
    }
    return (<>
        <div className="flex justify-start">
            <label htmlFor="imput-search" className="pr-2 font-semibold my-auto">{ label }</label>
            <input
                id="imput-search"
                label={ "imput-search" }
                type={ "text" }
                onChange={ HandleChange }
                onKeyDown={ HandleKeyEnter }

            />
            <IconButton
                onClick={ HandleClickIcon }
                className="pl-3 "
            > <IconSearch /> <span className="mr-2">Buscar</span>
            </IconButton>
        </div>
    </>)
}