

export default function Loading ({ children, isLoading = false }) {

    if (isLoading) {
        return (<div className="absolute w-full h-full bg-slate-100 opacity-80 z-50">
            <div id="loading" className="flex justify-center  items-center h-full w-full ">
                <svg className="animate-spin -ml-1 mr-3 h-20 w-20 text-color-primary" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="15" cy="15" r="10" strokeWidth="50"></circle>
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                </svg>
            </div>
        </div>)
    }
    else {
        return (
            <>{ children } </>
        )
    }
}