import { URL_SERVER_PDF } from "../../constants.js"
import { handlerError } from "../handlerError.js"
const URL_SERVER = URL_SERVER_PDF + "/reports/users"

export const GetReportsUsers = () => {
    try {
        let url = URL_SERVER
        return url
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
