import { useContext, useEffect, useState } from "react";
import PropTypes from 'prop-types';
import Table from "./Table";
import { DeleteCompany, GetCompanys } from "../services/company";
import { ROUTES } from "../constants";

import { AppContext } from "../context/applicationProvider";

const TableCompanys = ({ onSelect }) => {



    const [listCompanys, setListCompanys] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        setListCompanys(await GetCompanys(filter))
        appContext.setIsLoading(false)
    }
    const HandleOnDelete = (item) => {
        appContext.setIsLoading(true)
        DeleteCompany(item.id)
        appContext.setIsLoading(false)
    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["Codigo", "Nombre del Producto", "price sales", "price purchase"]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.COMPANY_NEW,
        linkToEdit: ROUTES.COMPANY_EDIT
    }
    return (< >
        <div className="w-full">
            <Table datas={ listCompanys } columns={ columns } onChangeSearch={ onchange } actions={ actions }>
            </ Table>
        </div></>
    )
}
TableCompanys.protoTypes = {
    onSelect: PropTypes.func,
}
TableCompanys.defaultProps = {
    onSelect: null
}

export default TableCompanys