import { useContext, useEffect, useState } from "react"
import { DeleteEmployee, GetEmployees } from "../services/employee"
import { ROUTES } from "../constants"
import Table from "./Table"

import { AppContext } from "../context/applicationProvider"

export default function TableEmployee () {
    const [listEmployees, setListEmployees] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {

        appContext.setIsLoading(true)
        setListEmployees(await GetEmployees(filter))
        appContext.setIsLoading(false)
    }
    const HandleOnDelete = (item) => {
        appContext.setIsLoading(true)
        DeleteEmployee(item.id)
        appContext.setIsLoading(false)
    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["Nombre", "Apellido", "email", "Telefono", "salario"]
    const indexColumnsHidden = [0, 6]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.EMPLOYEES_NEW,
        linkToEdit: ROUTES.EMPLOYEES_EDIT
    }
    return (<>
        <div className="w-full">
            <Table indexColumnsHidden={ indexColumnsHidden } datas={ listEmployees } columns={ columns } onChangeSearch={ onchange } actions={ actions }        >
            </Table>
        </div>
    </>
    )
}