import Button from "../../components/Button"
import { IconReportClients } from "../../components/Icons"
import Link from "../../components/Link";
import { GetReportsClients } from "../../services/reports/clients";

const ReportClients = () => {
    const linkClientList = () => {
        return GetReportsClients()
    }
    return <div className="flex flex-wrap  content-around">
        <Link href={ linkClientList() } label="lista de clientes"><IconReportClients /></Link>
    </div>
}
export default ReportClients