
import { IconReportProviders, } from "../../components/Icons";
import Link from "../../components/Link";
import { GetReportsProviders } from "../../services/reports/providers";

const ReportProviders = () => {
    const linkProvidersList = () => {
        return GetReportsProviders()
    }
    return <div className="flex flex-wrap  content-around">
        <Link href={ linkProvidersList() } label="lista de Proveedores"><IconReportProviders /></Link>
    </div>
}
export default ReportProviders