
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_PURCHASES = URL_SERVER + "/Purchases"

export const GetPurchases = async (filter) => {
    try {
        let url = URL_SERVER_PURCHASES
        if (filter != '') {
            const params = `?firstName=${ filter }&&lastName=${ filter }&&email=${ filter }&&phone=${ filter }`
            url = URL_SERVER_PURCHASES + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetPurchaseById = async (id = 0) => {
    try {
        let url = URL_SERVER_PURCHASES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdatePurchase = async ({ id, firstName, lastName, email, phone }) => {
    try {
        let url = URL_SERVER_PURCHASES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { firstName, lastName, email, phone })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreatePurchase = async (providerId) => {
    try {
        let url = URL_SERVER_PURCHASES
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { providerId })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeletePurchase = async (id) => {
    try {
        let url = URL_SERVER_PURCHASES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}

export const addPurchaseDetails = async ({ purchaseId, prodId, price, description, count }) => {
    try {
        let url = URL_SERVER_PURCHASES + '/details'
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { purchaseId, prodId, price, description, count })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
