import { useContext, useEffect, useState } from "react"
import PropTypes from 'prop-types';
import { DeleteActive, GetActiveItemsFree } from "../services/actives"
import { ROUTES } from "../constants"
import Table from './Table'

import { AppContext } from "../context/applicationProvider";

const TableActives = ({ onSelect, readOnly }) => {
    const [listActives, setListActives] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        const actives = await GetActiveItemsFree(filter)

        setListActives(actives.map(item => {
            return {
                id: item.id,
                codigo: item.codigo,
                description: item.description,
                inMaintenice: item.inMaintenice,
            }
        }))

        appContext.setIsLoading(false)
    }
    const HandleOnDelete = async (active) => {
        appContext.setIsLoading(true)
        const itemDeleted = await DeleteActive(active.id)
        const indexof = listActives.indexOf(x => x.id === active.id)
        if (indexof >= 0) {
            listActives.splice(itemDeleted, 1)
        }
        appContext.setIsLoading(false)
    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["codigo", "descripcion"]
    const indexColumnsHidden = [0]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.ACTIVES_NEW,
        linkToEdit: ROUTES.ACTIVES_EDIT
    }

    return (<>

        <div className="w-full">
            < Table indexColumnsHidden={ indexColumnsHidden } datas={ listActives } columns={ columns } onChangeSearch={ onchange } actions={ actions } onSelect={ onSelect } />
        </div ></>
    )
}
TableActives.protoTypes = {
    clientId: PropTypes.string,
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TableActives.defaultProps = {
    clientId: null,
    onSelect: null,
    readOnly: false
}

export default TableActives