
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_PROVIDERS = URL_SERVER + "/providers"

export const GetProviders = async (filter) => {
    try {
        let url = URL_SERVER_PROVIDERS
        if (filter != '') {
            const params = `?nameame=${ filter }&&phone=${ filter }&&address=${ filter }`
            url = URL_SERVER_PROVIDERS + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetProviderById = async (id = 0) => {
    try {
        let url = URL_SERVER_PROVIDERS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateProvider = async ({ id, name, phone, address, image }) => {
    try {
        let url = URL_SERVER_PROVIDERS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { name, phone, address, image })

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreateProvider = async ({ name, phone, address, image }) => {
    try {
        let url = URL_SERVER_PROVIDERS
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { name, phone, address, image })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeleteProvider = async (id) => {
    try {
        let url = URL_SERVER_PROVIDERS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}