import { useContext, useEffect, useState } from "react";
import useInput from "../hook/useInput";
import { CreateActive, GetActiveById, UpdateActive } from "../services/actives";
import TextField from "../components/TextField";
import { IconBarcode, IconText } from "../components/Icons";
import Button from "../components/Button";

import { useNavigate } from "react-router-dom";
import { AppContext } from "../context/applicationProvider";


/*********************FORM *********************/
const FormActives = ({ activesId }) => {

    const id = useInput("", 'id')
    const codigo = useInput("", 'Codigo')
    const description = useInput("", 'Descripcion')
    let appContext = useContext(AppContext)
    const navigate = useNavigate();
    const HandleOnSubmitForm = (env) => {
        appContext.setIsLoading(true)
        if (activesId) {
            UpdateActive({ id: id.props.value, codigo: codigo.props.value, description: description.props.value })

        } else {
            CreateActive({ id: id.props.value, codigo: codigo.props.value, description: description.props.value })
        }
        appContext.setIsLoading(false)
        navigate("/")

    }
    async function getData (activesId) {
        appContext.setIsLoading(true)
        const active = await GetActiveById(activesId)
        id.setValue(active.id)
        codigo.setValue(active.codigo)
        description.setValue(active.description)
        appContext.setIsLoading(false)
    }

    useEffect(() => {
        if (activesId) {
            getData(activesId)
        }
    }, [])


    return (
        <>

            <div >
                <h1 className="mb-5">Formulario Activos</h1>
                <div className='center border-2 border-color-segundary'  >
                    <TextField
                        { ...codigo.props }
                        name="Codigo"
                    >
                        <IconBarcode />
                    </TextField>
                    <TextField
                        { ...description.props }
                        name="description"
                    >
                        <IconText />
                    </TextField>

                </div>
                <div className="flex justify-center border-2 p-5 border-color-segundary">
                    <Button primary disabled={ codigo.error || description.error }
                        onClick={ HandleOnSubmitForm }>Guardar</Button>

                </div>
            </div >
        </>
    )
}
export default FormActives