import { useContext, useEffect, useState } from "react"
import Button from "../components/Button"
import { IconDolar } from "../components/Icons"
import TextField from "../components/TextField"
import useInput from "../hook/useInput"
import TablePayments from "./TablePayments"
import { GetPaymentBySalesId, addPayment } from "../services/payments"
import { GetSalesById } from "../services/sales"

import { AppContext } from "../context/applicationProvider"

const FormPayment = ({ noteSalesId }) => {

    const payment = useInput("", 'Monto  a Pagar')
    const [listPayments, setListPayments] = useState()
    const [sales, setSales] = useState()
    let appContext = useContext(AppContext)
    const HandleOnSubmitForm = async () => {
        appContext.setIsLoading(true)
        await addPayment(noteSalesId, payment.props.value)
        await getDatas(noteSalesId)
        appContext.setIsLoading(false)

    }
    const getDatas = async (noteSalesId) => {
        appContext.setIsLoading(true)
        let payments = await GetPaymentBySalesId(noteSalesId)
        payments = payments.map(item => {
            return {
                id: item.id,
                salId: item.salId,
                date: item.date,
                mount: item.mount,
            }
        })
        const sal = await GetSalesById(noteSalesId)
        setSales(sal)
        setListPayments(payments)
        appContext.setIsLoading(false)
    }
    useEffect(() => {
        if (noteSalesId) {
            getDatas(noteSalesId)
        }
    }, [noteSalesId])
    return (<>
        <div>
            <h1 className="mb-5">Pagos de Nota de Venta</h1>
            <div className=' border-2 border-color-segundary h-auto flex justify-center items-center'  >
                <div className="w-2/4 flex justify-around flex-col mb-5">
                    <div className="flex justify-between">
                        <span className="font-semibold" >Fecha : </span>
                        <span >{ sales?.date } </span>
                    </div>
                    <div className="flex justify-between">
                        <span className="font-semibold "> TotalNota : </span>
                        <span >{ sales?.totalWithDesc } </span></div>
                    <TablePayments payments={ listPayments }></TablePayments>
                    <div className="flex justify-between">
                        <span className="font-semibold "> Cancelado : </span>
                        <span>{ sales?.totalPayment } </span></div>
                    <div className="flex justify-between">
                        <span className="font-semibold "> Saldo : </span>
                        <span>{ sales?.due } </span></div>
                    <div className="flex justify-between">
                        <span className="font-semibold "> TotalNota : </span>
                        <span>{ sales?.totalWithDesc } </span></div>
                    <div className="flex justify-between">
                        <span><TextField
                            { ...payment.props }
                            name="Monto a pagar"
                        ><IconDolar /></TextField> </span></div>
                </div>
            </div >
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button primary disabled={ payment.error }
                    onClick={ HandleOnSubmitForm }>Guardar</Button>

            </div>
        </div>
    </>
    )
}
export default FormPayment