import { useContext, useEffect, useState } from "react"
import Table from "./Table"
import { AsignActiveItem, GetActivesByCliId } from "../services/actives"
import IconButton from "../components/IconButton"
import { IconActivos } from "../components/Icons"
import ModalPopup from "../components/ModalPopup"
import TableActives from "./TableActives"

import { AppContext } from "../context/applicationProvider"


/*********************FORM *********************/
const TableActiveAsign = ({ clientId }) => {

    const [openListActives, setOpenListActives] = useState(false)
    const [listActives, SetListActives] = useState([])
    let appContext = useContext(AppContext)
    const HandleOnDelete = async (item) => {
        appContext.setIsLoading(true)
        await AsignActiveItem(null, item.id)
        await getDatas(clientId)
        appContext.setIsLoading(false)
    }
    const handleCloseListActives = () => {

        setOpenListActives(false)
    }
    const HandleSelectActive = async (item) => {
        appContext.setIsLoading(true)
        await AsignActiveItem(clientId, item.id)
        await getDatas(clientId)
        setOpenListActives(false)
        appContext.setIsLoading(false)
    }
    const columns = ["codigo", "descripcion", "Maintenice"]
    const indexColumnsHidden = [0]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: null,
        linkToEdit: null
    }
    const getDatas = async (clientId) => {
        appContext.setIsLoading(true)
        const actives = await GetActivesByCliId(clientId)

        SetListActives(actives.map(item => {
            return {
                id: item.id,
                codigo: item.codigo,
                description: item.description,
                inMaintenice: item.inMaintenice,
            }
        }))
        appContext.setIsLoading(false)
    }
    useEffect(() => {
        getDatas(clientId)
    }, [clientId])
    return (<>
        <div >

            <div className="flex justify-center items-center flex-col">
                <div className="w-52">
                    <IconButton onClick={ (env) => { setOpenListActives(true) } }> <IconActivos /> <span className="mr-2">Asignar Activo</span>
                    </IconButton>
                </div>
                < Table className="w-full" indexColumnsHidden={ indexColumnsHidden } datas={ listActives } columns={ columns } actions={ actions } />
            </div>
            <div >
                <ModalPopup
                    open={ openListActives }
                    title={ "Lista de Activos" }
                    onClose={ handleCloseListActives }
                >
                    <TableActives onSelect={ HandleSelectActive } ></TableActives>
                </ModalPopup>
            </div>
        </div >
    </>
    )
}
export default TableActiveAsign