import { useEffect, useState } from "react"
import PropTypes from 'prop-types';
import useInput from "../hook/useInput";
import TableDetails from "./TableDetails";
import TextField from "../components/TextField";
import { IconPercent } from "../components/Icons";
import { SetSaveDescount } from "../services/sales";

const TableDetailsSales = (props) => {
    const [listDetails, setListDetails] = useState([])
    const [total, setTotal] = useState('0.00')
    const [desc, setDesc] = useState(props.desc)
    const [totalDesc, setTotalDesc] = useState('0.00')
    const [totalWithDesc, setTotalWithDesc] = useState('0.00')

    const descuento = useInput("", 'Descuento', (env) => {

        const percentage = env === '' ? desc : env
        setTotalDesc((percentage * total) / 100)
        setTotalWithDesc((Number(total) - Number((percentage * total) / 100)))
    }, async (DescountPercentage) => {
        await SetSaveDescount(DescountPercentage)
    }
    )

    useEffect(() => {
        setListDetails(props.datas)
    }, [props.datas])

    useEffect(() => {
        setTotal(props.total)
        descuento.setValue(props.desc)
        setTotalDesc((props.desc * Number(props.total)) / 100)
        setTotalWithDesc((Number(props.total) - Number((props.desc * Number(props.total)) / 100)))
    }, [props.total])



    return (<div className="flex ">
        <TableDetails rows={ listDetails } columns={ props.columns } onDelete={ props.onDelete } lastUpdated={ props.lastUpdated }></TableDetails>
        <div className="w-52 border-2 mt-5 p-5 bg-slate-300 uppercase font-semibold ">
            <div > <span className="">Total: </span><span className="float-right"> { total }</span></div>
            <div >

                <TextField
                    className="uppercase font-semibold"
                    { ...descuento.props }
                    type="number"
                    name="descuento"
                ><IconPercent /></TextField>
                <div className="w-full text-right " >{ totalDesc }</div>
            </div>
            <div className=" uppercase font-semibold"> <span >Total Nota:</span><span className=" float-right">{ totalWithDesc }</span></div>
        </div>
    </div>
    )
}
TableDetailsSales.protoTypes = {
    datas: PropTypes.array,
    total: PropTypes.string,
    desc: PropTypes.number,
    columns: PropTypes.array,
    onDelete: PropTypes.func,
    lastUpdated: PropTypes.string


}
TableDetailsSales.defaultProps = {

    datas: [],
    total: '0.00',
    desc: 0,
    columns: [],
    onDelete: () => {},
    lastUpdated: null


}

export default TableDetailsSales