import { useNavigate } from "react-router-dom"
import PropTypes, { string } from 'prop-types';
import TableInfinite from "../components/TableInfinite"

import FieldSearch from "../components/FieldSearch"
import { IconNew } from "../components/Icons";
import { prototype } from "react-modal";
import IconButton from "../components/IconButton";

const Table = ({ autonumeric, indexColumnsHidden, datas, columns, onChangeSearch, actions, onSelect, className, menuOption, isLoading }) => {
    const navegate = useNavigate()
    const HandleSelect = (itemSelected) => {
        onSelect(itemSelected)
    }
    const handleNewButton = () => {
        navegate(actions.linkToNew)
    }

    return (
        <div className={ className }>
            <div className=" flex justify-center">
                { actions.linkToNew && (
                    <div className="flex justify-between flex-row content-between  w-full m-5">
                        { onChangeSearch && (<span className="float-left"><FieldSearch label="Ingrese el texto a Buscar" onchange={ onChangeSearch } > </FieldSearch></span>) }
                        { actions.linkToNew && (<div className="float-right"><IconButton onClick={ handleNewButton }> <IconNew /> <span className="mr-2">Nuevo</span> </IconButton></div>) }
                    </div>
                )
                }
            </div>
            <TableInfinite
                autonumeric={ autonumeric }
                indexColumnsHidden={ indexColumnsHidden }
                rows={ datas }
                columns={ columns }
                onDelete={ actions?.onDelete }
                onEditLink={ actions?.linkToEdit }
                onSelect={ HandleSelect }
                menuOption={ menuOption }>
            </TableInfinite>
        </div >
    )
}
Table.protoTypes = {
    autonumeric: prototype.Bool,
    indexColumnsHidden: PropTypes.array,
    datas: PropTypes.array,
    columns: PropTypes.array,
    onChangeSearch: PropTypes.func,
    actions: PropTypes.shape({
        onDelete: PropTypes.func,
        linkToNew: PropTypes.string,
        linkToEdit: PropTypes.string
    }),
    onSelect: PropTypes.func,
    className: string,
    menuOption: PropTypes.shape({
        options: PropTypes.arrayOf({
            label: PropTypes.label,
            onClick: PropTypes.func
        }),
        icon: PropTypes.Any,
        label: PropTypes.label
    })

}
Table.defaultProps = {
    autonumeric: true,
    indexColumnsHidden: [],
    datas: [],
    columns: [],
    onChangeSearch: null,
    actions: {
        onDelete: null,
        linkToNew: null,
        linkToEdit: null,
    },
    onSelect: (itemSelected) => {},
    className: "",
    menuOption: null


}
export default Table