import { useEffect, useState } from "react"
import PropTypes from 'prop-types';
import TableDetails from "./TableDetails";

const TableDetailsPurchase = (props) => {
    const [listDetails, setListDetails] = useState([])
    const [total, setTotal] = useState('0.00')

    useEffect(() => {
        setListDetails(props.datas)
    }, [props.datas])

    useEffect(() => {
        setTotal(props.total)

    }, [props.total])



    return (<div className=" m-5 flex flex-col items-end">
        <TableDetails rows={ listDetails } columns={ props.columns } onDelete={ props.onDelete } lastUpdated={ props.lastUpdated }></TableDetails>
        <div className="w-1/4 border-2 mt-5 p-5 bg-slate-300 uppercase font-semibold">
            <div > <span className="">Total: </span><span className="float-right"> { total }</span></div>
        </div>
    </div>
    )
}
TableDetailsPurchase.protoTypes = {
    datas: PropTypes.array,
    total: PropTypes.string,
    desc: PropTypes.number,
    columns: PropTypes.array,
    onDelete: PropTypes.func,
    lastUpdated: PropTypes.string


}
TableDetailsPurchase.defaultProps = {

    datas: [],
    total: '0.00',
    desc: 0,
    columns: [],
    onDelete: () => {},
    lastUpdated: null


}

export default TableDetailsPurchase