
import { useContext, useEffect, useState } from "react";
import useInput from "../hook/useInput";
import { CreateEmployee, GetEmployeeById, UpdateEmployee } from "../services/employee";
import { IconUser } from "../components/Icons";
import Button from "../components/Button";
import TextField from "../components/TextField";
import { useNavigate } from "react-router-dom";

import { AppContext } from "../context/applicationProvider";

/*********************FORM *********************/
const FormEmployee = ({ employeeId }) => {

    const id = useInput("", 'id')
    const firstName = useInput("", 'Nombre')
    const lastName = useInput("", 'Apellido')
    const email = useInput("", 'Email')
    const phone = useInput("", 'Telefono')
    const salary = useInput("", 'Salario')
    const navegate = useNavigate()
    let appContext = useContext(AppContext)
    const HandleOnSubmitForm = (env) => {
        appContext.setIsLoading(true)
        if (employeeId) {
            UpdateEmployee({ id: id.props.value, firstName: firstName.props.value, lastName: lastName.props.value, email: email.props.value, phone: phone.props.value, salary: salary.props.value })

        } else {
            CreateEmployee({ id: id.props.value, firstName: firstName.props.value, lastName: lastName.props.value, email: email.props.value, phone: phone.props.value, salary: salary.props.value })
        }
        appContext.setIsLoading(false)
        navegate("/")
    }

    async function getData (empId) {
        appContext.setIsLoading(true)
        const employee = await GetEmployeeById(empId)
        id.setValue(employee.id)
        firstName.setValue(employee.firstName)
        lastName.setValue(employee.lastName)
        email.setValue(employee.email)
        phone.setValue(employee.phone)
        salary.setValue(employee.salary)
        appContext.setIsLoading(false)
    }

    useEffect(() => {
        if (employeeId) {
            getData(employeeId)
        }
    }, [employeeId])


    return (<>
        <div >
            <h1 className="mb-5">Formulario Empleados</h1>
            <div className='center border-2 border-color-segundary'  >
                <TextField
                    { ...firstName.props }
                    name="firstName"
                >
                    <IconUser />
                </TextField>
                <TextField
                    { ...lastName.props }
                    name="lastName"
                >  <IconUser /></TextField>
                <TextField
                    { ...email.props }
                    name="email"

                >  <IconUser /></TextField>
                <TextField
                    { ...phone.props }
                    name="phone"
                >  <IconUser /></TextField>
            </div>
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button primary disabled={ firstName.error || lastName.error || email.error || phone.error }
                    onClick={ HandleOnSubmitForm }>Guardar</Button>

            </div>
        </div >
    </>
    )
}
export default FormEmployee