import { useEffect, useState } from "react"

import PropTypes from 'prop-types';
import uuid from "react-uuid";
const IconButtonOptions = ({ item, menuOption }) => {
    const [optMenu, setOptMenu] = useState(menuOption)
    const [openMenu, setOpenMenu] = useState(false)

    const handleOnClick = (env) => {

        setOpenMenu(!openMenu)
    }
    useEffect(() => {

        setOptMenu(menuOption)
    }, [menuOption])
    return (
        <div className="relative" >
            <div onClick={ handleOnClick }  > { optMenu.icon } { optMenu.label }</div>
            { openMenu && <div className="absolute bg-slate-400 m-1 z-50 w-28">
                { optMenu.options.map(itemMenu => (

                    <div key={ uuid() } className=" flex flex-row justify-startsas items-center p-2 hover:bg-color-primary w-auto border-b-2" onClick={ () => itemMenu.onClick(item) }>

                        { itemMenu.label }

                    </div>


                )
                ) }

            </div> }
        </div>
    )
}
IconButtonOptions.protoTypes = {
    item: PropTypes.shape(),
    menuOption: PropTypes.shape({
        options: PropTypes.arrayOf({

            label: PropTypes.label,
            onClick: PropTypes.func
        }),
        icon: PropTypes.Any,
        label: PropTypes.label
    })
}
IconButtonOptions.defaultProps = {
    item: null,
    menuOption: {
        options: [],
        icon: null,
        label: ''
    }



}
export default IconButtonOptions