import { useParams } from "react-router-dom";
import FormProviders from "../../containers/FormProviders";
import MainTemplate from "../../template/MainTemplate";

function FormProviderPage () {

    let { providerId } = useParams()
    if (providerId === 'Nuevo' || providerId === 'nuevo') providerId = null
    return (
      
            <FormProviders providerId={ providerId }>
            </FormProviders>

      )
}

export default FormProviderPage;