import FormProductsPage from "./pages/products/FormProductsPage"
import FormActivesPage from "./pages/actives/FormActivesPage"
import UsersPage from "./pages/users/index"
import SalesPage from "./pages/Payments/index"
import ActivesPage from "./pages/actives"
import ReportsPage from "./pages/reportes";
import FormPurchasePage from "./pages/purchases";
