
import { KEY_CACHE_USER, KEY_CACHE_COMPANY, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { fakeCache } from "../data/fakeCache.js"
import { handlerError } from "./handlerError.js"

const URL_SERVER_SESSION = URL_SERVER + "/Login"
export const SignIn = async (user, password) => {
    try {
        let url = URL_SERVER_SESSION
        const response = await axios.post(url, { userName: user, password })
        return response.data
    }
    catch (error) {
        fakeCache.clear()
        handlerError(error); console.log(error)
    }

}
