import { useContext, useEffect, useState } from "react"
import PropTypes from 'prop-types';
import Table from './Table'
import { DeletePayment } from "../services/payments";

import { AppContext } from "../context/applicationProvider";

const TablePayments = ({ payments }) => {
    const [listPayments, setListPayments] = useState()
    let appContext = useContext(AppContext)

    const HandleOnDelete = async (payment) => {
        appContext.setIsLoading(true)
        const itemDeleted = await DeletePayment(payment.id)
        const indexof = TablePayments.indexOf(x => x.id === payment.id)
        if (indexof !== -1) {
            TablePayments.splice(itemDeleted, 1)
        }
        appContext.setIsLoading(false)

    }
    useEffect(() => {
        setListPayments(payments)
    }, [payments])
    const columns = ["id", "Fecha", "mount"]
    const indexColumnsHidden = [0]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: null,
        linkToEdit: null
    }

    return (<>
        <div className="w-full">
            < Table autonumeric={ false }
                indexColumnsHidden={ indexColumnsHidden }
                datas={ listPayments }
                columns={ columns }
                actions={ actions }
            />
        </div ></>
    )
}
TablePayments.protoTypes = {
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TablePayments.defaultProps = {
    onSelect: null,
    readOnly: false
}

export default TablePayments