import { useContext, useEffect, useState } from "react"
import PropTypes from 'prop-types';
import { GetClients } from "../services/clients"
import Table from './Table'

import { AppContext } from "../context/applicationProvider";

const TableClientSales = ({ onSelect }) => {
    const [listClients, setListClients] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        const listCli = (await GetClients(filter)).map(item => {
            return {
                id: item.id,
                firstName: item.firstName,
                lastName: item.lastName,
                deuda: item.deuda
            }
        })
        setListClients(listCli)
        appContext.setIsLoading(false)
    }
    const onchange = async (value) => {
        if (value.length === 0) {
            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    const HandleSelect = async (itemSelected) => {
        appContext.setIsLoading(true)
        await onSelect(itemSelected)
        appContext.setIsLoading(false)
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["N", "Nombre", "Apellido", "Deuda"]

    return (<>

        <div className="m-5">
            < Table datas={ listClients } columns={ columns } onChangeSearch={ onchange } onSelect={ HandleSelect } />
        </div>
    </>
    )
}
TableClientSales.protoTypes = {
    onSelect: PropTypes.func

}
TableClientSales.defaultProps = {
    onSelect: null,

}

export default TableClientSales