import { useParams } from "react-router-dom"
import FormActivesAsign from "../../containers/FormActivesAsign"

function ActivesPage () {
    let { clientId } = useParams()    
    return (
        <div className=' w-full m-5'>
            <FormActivesAsign clientId={ clientId }></FormActivesAsign>
        </div>
    )
}

export default ActivesPage