
import { useContext, useEffect, useState } from "react";
import useInput from "../hook/useInput";
import { CreateUser, GetUserById, UpdateUser } from "../services/users";
import { IconUser } from "../components/Icons";
import TextField from "../components/TextField";
import Button from "../components/Button";
import { useNavigate } from "react-router-dom";
import { GetCompanys } from "../services/company";
import { ROUTES } from "../constants";
import SelectBox from "../components/SelectBox";

import { AppContext } from "../context/applicationProvider";

/*********************FORM *********************/
export default function FormUser ({ userId }) {

    const id = useInput("", 'id')
    const userName = useInput("", 'Nombre')
    const email = useInput("", 'Email')
    const [companySelected, setCompanySelected] = useState()
    const [listCompany, setListCompany] = useState([])
    let appContext = useContext(AppContext)
    const navigate = useNavigate();
    const HandleOnSubmitForm = async (env) => {
        appContext.setIsLoading(true)
        let result = null
        if (userId) {
            result = await UpdateUser({ id: id.props.value, userName: userName.props.value, email: email.props.value })
        } else {
            result = await CreateUser({ id: id.props.value, userName: userName.props.value, email: email.props.value, company: companySelected })
        }
        appContext.setIsLoading(false)
        if (result) {
            navigate(ROUTES.USERS)
        }
    }
    const GetDataCompany = async () => {
        const companys = await GetCompanys()
        const comp = companys.map(item => {
            return {
                id: item.id,
                label: item.name
            }
        })
        setListCompany(comp)

    }
    const GetDataUser = async (userId) => {
        if (userId) {
            const user = await GetUserById(userId)
            id.setValue(user.id)
            userName.setValue(user.userName)
            email.setValue(user.email)
        }
    }
    const getData = async (userId) => {
        appContext.setIsLoading(true)
        await GetDataUser(userId)
        await GetDataCompany()
        appContext.setIsLoading(false)
    }
    const handleOnChange = (itemSelected) => {
        setCompanySelected(itemSelected)
    }
    useEffect(() => {
        getData(userId)
    }, [])


    return (<>

        <div>
            <h1 className="mb-5">Formulario Usuarios</h1>
            <div className='center border-2 border-color-segundary'  >
                <TextField
                    { ...userName.props }
                    name="userName"
                > <IconUser /></TextField>
                <TextField
                    { ...email.props }
                    name="email"
                ><IconUser /></TextField>
                <SelectBox options={ listCompany } label={ "Compañia" } onChange={ handleOnChange }> </SelectBox>
            </div>
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button primary disabled={ userName.error || email.error }
                    onClick={ HandleOnSubmitForm }>Guardar</Button>
            </div>
        </div>
    </>
    )
}