import { useContext, useEffect, useState } from "react";
import useInput from "../hook/useInput";
import { CreateCompany, GetCompanyById, UpdateCompany } from "../services/company";
import TextField from "../components/TextField";
import { IconUser } from "../components/Icons";
import Button from "../components/Button";
import { useNavigate } from "react-router-dom";

import { AppContext } from "../context/applicationProvider";

/*********************FORM *********************/
const FormCompany = ({ companyId }) => {
    const id = useInput("", 'id')
    const name = useInput("", 'Nombre')
    const phone = useInput("", 'Telefono')
    const address = useInput("", 'Email')
    const navigate = useNavigate();
    let appContext = useContext(AppContext)
    const HandleOnSubmitForm = (env) => {
        appContext.setIsLoading(true)
        if (companyId) {
            UpdateCompany({ id: id.props.value, name: name.props.value, phone: phone.props.value, address: address.props.value })
        } else {
            CreateCompany({ name: name.props.value, phone: phone.props.value, address: address.props.value })
        }
        appContext.setIsLoading(false)
        navigate("/")

    }
    async function getData (companyId) {
        appContext.setIsLoading(true)
        const company = await GetCompanyById(companyId)
        id.setValue(company.id)
        name.setValue(company.name)
        phone.setValue(company.phone)
        address.setValue(company.address)
        appContext.setIsLoading(false)
    }
    useEffect(() => {
        if (companyId) {
            getData(companyId)
        }
    }, [])


    return (<>

        <div >
            <h1 className="mb-5">Formulario Company</h1>
            <div className='center border-2 border-color-segundary'  >
                <TextField
                    { ...name.props }
                    name="name"

                > <IconUser></IconUser></TextField>
                <TextField
                    { ...phone.props }
                    name="phone"

                ><IconUser /> </TextField>
                <TextField
                    { ...address.props }
                    name="address"
                ><IconUser /></TextField>

            </div>
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button disabled={ name.error || phone.error || address.error }
                    primary
                    onClick={ HandleOnSubmitForm }>Guardar</Button>

            </div>
        </div >
    </>
    )
}
export default FormCompany