
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_ACTIVES = URL_SERVER + "/actives"

export const GetActives = async (filter) => {
    try {
        let url = URL_SERVER_ACTIVES
        if (filter !== '') {
            const params = `?codigo=${ filter }&&description=${ filter }`
            url = URL_SERVER_ACTIVES + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        console.log(response)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetActivesByCliId = async (cliId = '') => {
    try {
        let url = URL_SERVER_ACTIVES
        const params = `?cliId=${ cliId }`
        url = URL_SERVER_ACTIVES + params
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        console.log(response)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetActiveById = async (id = 0) => {
    try {
        let url = URL_SERVER_ACTIVES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateActive = async ({ id, codigo, description }) => {
    try {
        let url = URL_SERVER_ACTIVES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { codigo, description })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
