
import { useState } from 'react';
import uuid from "react-uuid";
import PropTypes from 'prop-types';
import ModalConfirmation from '../components/ModalConfirmation';
import { IconTrash } from '../components/Icons';
import IconButton from '../components/IconButton';

const TableDetails = ({ columns, rows, onDelete, lastUpdated }) => {
    const columnAutoNumeric = (indexColumn) => {
        return (<th key={ uuid() } >
            { indexColumn }
        </th>)
    }
    const printRowsHeader = (column) => {
        return column.map((col, index) => {
            return (<th key={ uuid() } >{ col }</th>)
        })

    }
    const printRows = (row, indexRows) => {
        let bgRowColor = "bg-white"
        if (row.id === lastUpdated) {
            bgRowColor = "bg-slate-400"
        }
        return (
            <tr
                key={ uuid() }
                className={ 'hover:bg-slate-400 hover:text-white border-2 ' + bgRowColor }
            >
                { columnAutoNumeric(indexRows + 1) }
                <td style={ { display: 'none' } } > { row.id } </td>
                <td width={ '20%' } > { row.prodId } </td>
                <td width={ '50%' }> { row.description } </td>
                <td width={ '10%' } > { row.price } </td>
                <td width={ '10%' }> { row.count } </td>
                <td width={ '10%' }> { row.subTotal } </td>

                { onDelete && (
                    <td  width={ 10 }>
                        <IconButton aria-label="delete" onClick={ (env) => HandleDelete(row) }>  <IconTrash /> </IconButton>
                    </td>)
                }
            </tr>
        )

    }
    const [showModal, SetShowModal] = useState(false)
    const [itemSelected, setItemSelected] = useState(null)
    const HandleDelete = (element) => {

        setItemSelected(element)
        SetShowModal(true)
    }
    const HandleOnclickOkModal = () => {
        SetShowModal(false)
        HandleToDelete(itemSelected)
    }
    const HandleOnclickCancelModal = () => {
        SetShowModal(false)
    }
    const HandleToDelete = (itemSelected) => {
        onDelete(itemSelected)
    }
    return (<div className='w-full'>
        <ModalConfirmation show={ showModal } messageText="Esta Seguro de Eliminar ?"
            onOkClick={ HandleOnclickOkModal }
            onCancelClick={ HandleOnclickCancelModal }
            title="Confirmacion de eliminacion">
        </ModalConfirmation>

        <div className=' h-128 overflow-y-auto border-2' >
            <table height={ '10px' } className='w-full'>
                <thead className='  w-full'>
                    <tr >
                        <th  >N</th>
                        { printRowsHeader(columns) }
                        { onDelete && (<th  ></th>) }


                    </tr>
                </thead>
                <tbody className=' '>
                    { rows.map((row, indexRow) => {
                        return printRows(row, indexRow)
                    })
                    }
                </tbody>
            </table>
        </div>
    </div >);
}
TableDetails.protoTypes = {
    columns: PropTypes.array,
    rows: PropTypes.arrayOf({
        id: PropTypes.string,
        prodId: PropTypes.string,
        description: PropTypes.string,
        price: PropTypes.string,
        count: PropTypes.string,
        subTotal: PropTypes.string
    }),
    onDelete: PropTypes.func,
    lastUpdated: PropTypes.string
}

TableDetails.defaultProps = {
    columns: [],
    rows: [],
    onDelete: null,
    lastUpdated: null
}
export default TableDetails