import { useContext, useEffect, useState } from "react";
import PropTypes from 'prop-types';
import { DeletePurchase, GetPurchases } from "../services/purchases";
import Table from "./Table";
import { ROUTES } from "../constants";

import { AppContext } from "../context/applicationProvider";

const TablePurchases = ({ onSelect, readOnly }) => {
    const [listPurchases, setListPurchases] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        setListPurchases(await GetPurchases(filter))
        appContext.setIsLoading(false)
    }

    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    const HandleOnSelect = (item) => {
        onSelect(item)
    }
    const HandleOnDelete = async (item) => {
        appContext.setIsLoading(true)
        await DeletePurchase(item.id)
        getDatas()
        appContext.setIsLoading(false)
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["N de Nota", "Fecha de Nota", "Total"]
    const indexColumnsHidden = [3]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.PURCHASES_NEW,
        linkToEdit: ROUTES.PURCHASES_EDIT
    }
    return (<>
        <div className="w-full">
            <Table indexColumnsHidden={ indexColumnsHidden }
                datas={ listPurchases }
                columns={ columns }
                onChangeSearch={ onchange }
                actions={ actions }
                onSelect={ HandleOnSelect } />
        </div>
    </>
    )
}
TablePurchases.protoTypes = {
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TablePurchases.defaultProps = {
    onSelect: () => {},
    readOnly: false
}
export default TablePurchases