import { useContext, useEffect, useState } from "react";
import IconButton from "../components/IconButton";
import { IconAdd, IconFind, IconPrint, IconSearch, IconText } from "../components/Icons";
import ModalPopup from "../components/ModalPopup";

import TableProductsPurchase from "./TableProductsPurchase";
import TableProviders from "./TableProviders";
import TableDetailsPurchase from "./TableDetailsPurchase";
import { CreatePurchase, GetPurchaseById, addPurchaseDetails } from "../services/purchases";
import TextField from "../components/TextField";
import useInput from "../hook/useInput";
import { ROUTES } from "../constants";
import { useNavigate } from "react-router-dom";
import Button from "../components/Button";

import { GetReportsNotePurchase } from "../services/reports/purchases";
import { AppContext } from "../context/applicationProvider";

const FormPurchase = ({ purchaseId }) => {


    const [notePurchase, SetNotePurchase] = useState()

    const [productSelected, SetProductSelected] = useState(null)

    const [openListProviders, setOpenListProviders] = useState(false)
    const [openListProducts, setOpenListProducts] = useState(false)
    const columnsDetails = ["Codigo", "descripcion", "precio", "cantidad", "subTotal"]
    const indexColumnsHidden = [0]
    const pricePurchases = useInput("", 'Precio de Compra')
    const countPurchase = useInput("", 'Cantidad ',)
    let appContext = useContext(AppContext)
    const navigate = useNavigate();

    const HandleSelectProduct = async (item) => {
        setOpenListProducts(false)
        SetProductSelected(item)
    }

    const HandleSelectProviders = async (item) => {
        setOpenListProviders(false)
        if (purchaseId) {
            navigate(ROUTES.PURCHASES_EDIT + "/" + purchaseId)
        } else {
            const purchase = await CreatePurchase(item.id)
            navigate(ROUTES.PURCHASES_EDIT + "/" + purchase.id)
        }
    }

    const handleDeleted = () => {}
    const HandlePrintPurchase = async (env) => {
        window.open(GetReportsNotePurchase(purchaseId), '_blank');
    }
    const handleAddProduct = async () => {
        const detail = {
            purchaseId: purchaseId, prodId: productSelected.id, price: pricePurchases.props.value, description: productSelected.description, count: countPurchase.props.value
        }
        const note = await addPurchaseDetails(detail)
        SetNotePurchase(note)
        SetProductSelected(null)
        pricePurchases.setValue("")
        countPurchase.setValue("")

    }

    const getDatas = async (purId) => {
        appContext.setIsLoading(true)
        SetNotePurchase(await GetPurchaseById(purId))
        appContext.setIsLoading(false)

    }
    useEffect(() => {
        if (purchaseId) {
            getDatas(purchaseId)
        }
    }, [purchaseId])

    return (<>

        <div className="w-full" >
            <div className=" flex justify-center border-2 p-5">
                { notePurchase?.provider && <div>
                    <span className="font-semibold uppercase" >Proveedor : </span> <span>{ notePurchase.provider.id } </span><br />
                    <span> <span className="font-semibold uppercase"> Nombre :</span>{ notePurchase.provider.name } </span><br />
                    <span><span className="font-semibold uppercase">Direccion :  </span>{ notePurchase.provider.address }</span><br />
                    <span><span className="font-semibold uppercase"> Telefono : </span>{ notePurchase.provider.phone }</span>
                </div> }
                <div className="p-5">
                    <IconButton onClick={ () => setOpenListProviders(true) } className="inline-block float-left">
                        <IconSearch />
                        Buscar Proveedor
                    </IconButton>
                </div>
            </div>
            { purchaseId &&
                <div>
                    <div className="flex justify-center items-center p-5 border-2 bg-slate-300 mt-2">
                        <div className="">
                            <IconButton onClick={ () => setOpenListProducts(true) }>
                                <IconFind />
                                Buscar Producto
                            </IconButton></div>
                        { productSelected && <div className="flex justify-start flex-col w-1/3">
                            <span className="font-semibold uppercase" >id : </span> <span>{ productSelected.id }
                            </span><br />
                            <span><span className="font-semibold uppercase">Descripcion :  </span>{ productSelected.description }</span><br />
                            <div className="flex justify-around">
                                <TextField
                                    { ...pricePurchases.props }
                                    name="PricePurchases"                        >
                                    <IconText />
                                </TextField>
                                <TextField
                                    { ...countPurchase.props }
                                    name="countPurchase"                        >
                                    <IconText />
                                </TextField>
                            </div>
                        </div>
                        }
                        { productSelected && <div className="">
                            <IconButton onClick={ (env) => handleAddProduct() }>
                                <IconAdd />
                                Agregar
                            </IconButton>
                        </div> }
                    </div>
                    <TableDetailsPurchase indexColumnsHidden={ indexColumnsHidden }
                        datas={ notePurchase?.details }
                        desc={ 0 }
                        columns={ columnsDetails }

                        onDelete={ handleDeleted }
                    ></TableDetailsPurchase>
                    <div className="flex justify-center mt-5">
                        <Button primary={ true } className=""
                            onClick={ HandlePrintPurchase }><IconPrint />Nota de Compra</Button>
                    </div>
                </div>
            }
            <div className='list-object'>
                <div className='list-Products'>
                    <ModalPopup
                        fullScreen
                        open={ openListProducts }
                        title={ "Lista de Productos" }
                        onClose={ () => setOpenListProducts(false) }
                    >
                        <TableProductsPurchase onSelect={ HandleSelectProduct }></TableProductsPurchase>
                    </ModalPopup>
                </div>
                <div className='list-Clients'>
                    <ModalPopup
                        open={ openListProviders }
                        title={ "Lista de Proveedores" }
                        onClose={ () => setOpenListProviders(false) }
                    >
                        <TableProviders onSelect={ HandleSelectProviders } ></TableProviders>
                    </ModalPopup>
                </div>
            </div>
        </div >
    </>
    )
}
export default FormPurchase