import { fakeCache } from "../data/fakeCache";

export const handlerError = (error) => {

    if (!error.response) {
        window.location.href = "/login"
        fakeCache.clear();
    } else {

        switch (error.response.status) {
            case 403:
                window.location.href = "/login"
                fakeCache.clear();
                break
            default: window.location.href = "/"
        }
    }
}