
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_PRODUCTS = URL_SERVER + "/products"

export const GetProducts = async (filter) => {
    try {
        let url = URL_SERVER_PRODUCTS
        if (filter !== '') {
            const params = `?description=${ filter }`
            url = URL_SERVER_PRODUCTS + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetProductById = async (id = 0) => {
    try {
        let url = URL_SERVER_PRODUCTS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateProduct = async ({ id, description, priceSales, pricePurchase, image, prvId }) => {
    try {
        let url = URL_SERVER_PRODUCTS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { description, priceSales, pricePurchase, image, prvId })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreateProduct = async ({ description, priceSales, pricePurchase, image, prvId }) => {
    try {
        let url = URL_SERVER_PRODUCTS
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { description, priceSales, pricePurchase, image, prvId })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeleteProduct = async (id) => {
    try {
        let url = URL_SERVER_PRODUCTS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}