import { URL_SERVER_PDF } from "../../constants.js"
import { handlerError } from "../handlerError.js"
const URL_SERVER = URL_SERVER_PDF + "/reports/purchases"

export const GetReportsPurchases = () => {
    try {
        let url = URL_SERVER
        return url
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetReportsNotePurchase = (purchaseId) => {
    try {
        let url = URL_SERVER + "/NotePurchase/" + purchaseId
        return url
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}