import { useContext } from 'react';
import Header from './Header';

import { Outlet } from 'react-router-dom';
import { AppContext } from '../context/applicationProvider';
import Footer from '../components/Footer';
import Loading from '../components/Loading';

export default function MainTemplate ({ children }) {

    let appContext = useContext(AppContext)


    return (
        < >
            <Loading isLoading={ appContext.isLoading }></Loading>
            <Header title={ appContext.navegatorSelectted } />
            <div className='sm:w-11/12 flex justify-center mx-auto items-start min-h-screen frame'>
                <Outlet />
            </div>
            <div className=' w-full flex flex-wrap self-end'>
                <Footer></Footer>
            </div>
        </>
    );
}