import { useContext, useEffect, useState } from "react";
import PropTypes from 'prop-types';
import { GetProducts } from "../services/products";
import Table from "./Table";

import { AppContext } from "../context/applicationProvider";

const TableProductsSales = (prop) => {
    const [listProducts, setListProducts] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        const listProds = (await GetProducts(filter)).map(item => {
            return {
                id: item.id,
                description: item.description,
                priceSales: item.priceSales,
                image: item.image,
            }
        })
        setListProducts(listProds)
        appContext.setIsLoading(false)
    }
    const HandleSelect = async (itemSelected) => {
        //setIsLoading(true)
        await prop.onSelect(itemSelected)
        //setIsLoading(false)
    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["Codigo", "Nombre del Producto", "price sales", "image"]
    const indexColumnsHidden = []
    return (<>


        < div className="mt-5">
            { (<Table indexColumnsHidden={ indexColumnsHidden } datas={ listProducts } columns={ columns } onChangeSearch={ onchange } onSelect={ HandleSelect } />) }

        </div>
    </>
    )
}
TableProductsSales.protoTypes = {
    onSelect: PropTypes.func,

}
TableProductsSales.defaultProps = {
    onSelect: null,

}
export default TableProductsSales