import { useContext, useEffect, useState } from "react";
import Table from "./Table";
import { ROUTES } from "../constants";
import { IconSearch } from "../components/Icons";
import ModalPopup from "../components/ModalPopup";
import TableClientSales from "./TableClientSales";
import IconButton from "../components/IconButton";
import { GetClientById } from "../services/clients";
import FormPayment from "./FormPayment";

import { AppContext } from "../context/applicationProvider";

const Payments = ({ clientId }) => {
    const [client, SetClient] = useState()
    const [noteSelected, SetNoteSelected] = useState()
    const [openListClients, SetOpenListClients] = useState(false)
    const [openFormPayment, SetOpenFormPayment] = useState(false)
    const [listSales, setListSales] = useState([])
    let appContext = useContext(AppContext)

    const handleOpenListClients = () => {
        SetOpenListClients(true)
    }
    const handleCloseListClients = () => {
        SetOpenListClients(false)
    }
    const onSelectNote = (item) => {

        SetNoteSelected(item)
        SetOpenFormPayment(true)
    }

    const HandleSelectClient = async (clItem) => {

        window.location.replace(ROUTES.PAYMENTS + "/" + clItem.id)
    }

    const getClient = async (clientId) => {
        appContext.setIsLoading(true)
        const clItem = await GetClientById(clientId)
        SetClient(clItem)
        setListSales(clItem.notesDue.map(item => {
            return {
                id: item.id,
                date: item.date,
                total: item.total,
                totalDesc: item.totalDesc,
                totalWithDesc: item.totalWithDesc,
                totalPayment: item.totalPayment,
                due: item.due,
            }
        }))
        appContext.setIsLoading(false)
    }

    useEffect(() => {

        if (clientId) {
            getClient(clientId)
        }
    }, [clientId])
    const columns = ["N de Nota", "Fecha", "Total", "Desc", "Monto a Pagar", "Cancelado", "Deuda"]
    const indexColumnsHidden = []
    const actions = {
        onDelete: null,
        linkToNew: null,
        linkToEdit: null
    }

    return (
        <>
            <div className="w-full">
                <h1 className="">Pagos de Notas de Venta</h1>
                <div className=" border-2 my-3 px-3 py-2 uppercase h-16">
                    <div className="inline-block    w-10/12 ">
                        <div><span className="font-semibold" >Cliente : </span> <span>{ client?.id } </span><br />
                            <span className="font-semibold "> { client?.firstName } { client?.lastName }</span>
                        </div>
                    </div>
                    <div className=" pl-2 inline-block w-2/12 float-right ">
                        <IconButton onClick={ handleOpenListClients }>
                            <IconSearch />  <span className="mr-2">Buscar</span>
                        </IconButton>
                    </div>
                </div>
                <div className="m-2">
                    <Table autonumeric={ false }
                        indexColumnsHidden={ indexColumnsHidden }
                        datas={ listSales }
                        columns={ columns }
                        onSelect={ onSelectNote }
                        actions={ actions } />
                </div>
                <div className='Payment'>
                    <ModalPopup
                        open={ openFormPayment }
                        title={ "Pagar" }
                        onClose={ () => SetOpenFormPayment(false) }
                    >
                        <FormPayment noteSalesId={ noteSelected?.id } ></FormPayment>
                    </ModalPopup>
                </div>
                <div className='list-Clients'>
                    <ModalPopup
                        open={ openListClients }
                        title={ "Lista de Clientes" }
                        onClose={ handleCloseListClients }
                    >
                        <TableClientSales onSelect={ HandleSelectClient } ></TableClientSales>
                    </ModalPopup>
                </div>
            </div>
        </>
    )
}
Payments.protoTypes = {
}
Payments.defaultProps = {

}
export default Payments