import { useContext, useEffect, useState } from "react"
import PropTypes from 'prop-types';
import { DeleteUser, GetUsers } from "../services/users"
import { ROUTES } from "../constants"
import Table from './Table'

import { AppContext } from "../context/applicationProvider";

const TableUsers = ({ onselect, readOnly }) => {
    const [listUsers, setListUsers] = useState([])
    let appContext = useContext(AppContext)
    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        setListUsers(await GetUsers(filter))
        console.log(listUsers)
        appContext.setIsLoading(false)
    }
    const HandleOnDelete = async (user) => {
        const itemDeleted = await DeleteUser(user.id)
        const indexof = listUsers.indexOf(x => x.id === user.id)
        if (indexof !== -1) {
            listUsers.splice(itemDeleted, 1)
        }

    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["userName", "email"]
    const indexColumnsHidden = [0]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.USERS_NEW,
        linkToEdit: ROUTES.USERS_EDIT
    }

    return (
        <>

            <div className="w-full">
                < Table indexColumnsHidden={ indexColumnsHidden } datas={ listUsers } columns={ columns } onChangeSearch={ onchange } actions={ actions } isLoading />
            </div >
        </>
    )
}
TableUsers.protoTypes = {
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TableUsers.defaultProps = {
    onSelect: null,
    readOnly: false
}

export default TableUsers