
import { IconReportProduct, } from "../../components/Icons";
import Link from "../../components/Link";
import { GetReportsProducts } from "../../services/reports/products";

const ReportProducts = () => {
    const linkProductList = () => {
        return GetReportsProducts()
    }
    return <div className="flex flex-wrap  content-around">
        <Link href={ linkProductList() } label="lista de Productos"><IconReportProduct /></Link>
    </div>
}
export default ReportProducts