import { useContext, useEffect, useState } from "react";
import useInput from "../hook/useInput";
import { CreateClient, GetClientById, UpdateClient } from "../services/clients";
import { IconEmail, IconPhone, IconUser } from "../components/Icons";
import TextField from "../components/TextField";
import Button from "../components/Button";
import { useNavigate } from "react-router-dom";

import { AppContext } from "../context/applicationProvider";

/*********************FORM *********************/
const FormClient = ({ clientId }) => {
    let appContext = useContext(AppContext)
    const id = useInput("", 'id')
    const firstName = useInput("", 'Nombre')
    const lastName = useInput("", 'Apellido')
    const email = useInput("", 'Email')
    const phone = useInput("", 'Telefono')
    const navegate = useNavigate()
    const HandleOnSubmitForm = (env) => {
        appContext.setIsLoading(true)
        if (clientId) {
            UpdateClient({ id: id.props.value, firstName: firstName.props.value, lastName: lastName.props.value, email: email.props.value, phone: phone.props.value })

        } else {
            CreateClient({ id: id.props.value, firstName: firstName.props.value, lastName: lastName.props.value, email: email.props.value, phone: phone.props.value })
        }
        appContext.setIsLoading(false)
        navegate("/")
    }

    const getData = async (clientId) => {
        appContext.setIsLoading(true)
        const client = await GetClientById(clientId)
        id.setValue(client.id)
        firstName.setValue(client.firstName)
        lastName.setValue(client.lastName)
        email.setValue(client.email)
        phone.setValue(client.phone)
        appContext.setIsLoading(false)
    }

    useEffect(() => {
        if (clientId) {
            getData(clientId)
        }
    }, [])


    return (<>
        <div >
            <h1 className="mb-5">Formulario Clientes</h1>
            <div className='center border-2 border-color-segundary'  >
                <TextField
                    { ...firstName.props }
                    name="firstName"
                ><IconUser /></TextField>
                <TextField
                    { ...lastName.props }
                    name="lastName"
                > <IconUser /></TextField>
                <TextField
                    { ...email.props }
                    name="email"
                ><IconEmail /></TextField>
                <TextField
                    { ...phone.props }
                    name="phone"
                > <IconPhone /></TextField>
            </div>
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button primary disabled={ firstName.error || lastName.error || email.error || phone.error }
                    onClick={ HandleOnSubmitForm }>Guardar</Button>
            </div>
        </div >
    </>
    )
}
export default FormClient