
import { IconReportActives } from "../../components/Icons";
import Link from "../../components/Link";
import { GetReportsActives, GetReportsActivesAssigned, GetReportsActivesNoAssigned } from "../../services/reports/actives";

const ReportActives = () => {
    const linkActivesList = () => {
        return GetReportsActives()
    }
    const linkActivesNoAssignedList = () => {
        return GetReportsActivesNoAssigned()
    }
    const linkActivesAssignedList = () => {
        return GetReportsActivesAssigned()
    }
    return <div className="flex flex-wrap  content-around">
        <Link href={ linkActivesList() } label="lista de Activos"><IconReportActives /></Link>
        <Link href={ linkActivesAssignedList() } label="lista de Activos Prestados"><IconReportActives /></Link>
        <Link href={ linkActivesNoAssignedList() } label="lista de Activos No Prestados"><IconReportActives /></Link>
    </div>
}
export default ReportActives