
import { useState } from 'react';
import uuid from "react-uuid";
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import ModalConfirmation from './ModalConfirmation';
import { IconEdit, IconTrash } from './Icons';
import IconButton from './IconButton';
import IconButtonOptions from './IconButtonOptions';



const TableInfinite = ({ autonumeric, indexColumnsHidden, columns, rows, onDelete, onEditLink, onSelect, menuOption }) => {
    const columnAutoNumeric = (indexColumn) => {
        return (<td key={ indexColumn } >
            { indexColumn }
        </td>)
    }
    const printRowsHeader = (columns) => {

        return columns.map((col, index) => {
            return (<th key={ col + index } >{ col }</th>)
        })

    }
    const printRows = (row) => {
        let keys = Object.keys(row);
        return (
            keys.map((k, index) => {
                let displayCell = ""
                if (indexColumnsHidden.findIndex(x => x === index) !== -1) {
                    displayCell = "hidden"
                }

                return (<td key={ k + index } className={ displayCell }>
                    { row[k] }
                </td>)

            }
            )
        )
    }
    const [showModal, SetShowModal] = useState(false)
    const [itemSelected, setItemSelected] = useState(null)
    const HandleSelect = async (itemIdSelected) => {
        console.log(itemIdSelected)
        await onSelect(itemIdSelected)
    }
    const HandleDelete = (element) => {

        setItemSelected(element)
        SetShowModal(true)
    }
    const HandleOnclickOkModal = async () => {
        SetShowModal(false)

        await onDelete(itemSelected)

    }
    const HandleOnclickCancelModal = () => {
        SetShowModal(false)
    }

    return (<>
        <ModalConfirmation show={ showModal } messageText="Esta Seguro de Eliminar ?"
            onOkClick={ HandleOnclickOkModal }
            onCancelClick={ HandleOnclickCancelModal }
            title="Confirmacion de eliminacion">
        </ModalConfirmation>

        <div className='mt-5 flex justify-center  m-5' >
            <table>
                <thead className=''>
                    <tr  >
                        { autonumeric && (<th  >N</th>) }
                        { printRowsHeader(columns) }
                        { onEditLink && (<th  ></th>) }
                        { onDelete && (<th  ></th>) }

                    </tr>
                </thead>
                <tbody>
                    {
                        rows.map((row, indexRows) => {

                            return (

                                <tr
                                    key={ uuid() }
                                    className='hover:bg-slate-400 hover:text-white border-2 hover:cursor-pointer'
                                    onDoubleClick={ (env) => { HandleSelect(row) } }

                                >
                                    { autonumeric && columnAutoNumeric(indexRows + 1) }
                                    { printRows(row, indexRows + 1) }
                                    { onEditLink && (
                                        <td className='w-2 '>
                                            <div className=" mx-2 hover:border-color-primary">
                                                <Link to={ onEditLink + "/" + row.id } >
                                                    <div className="hover:cursor-pointer p-2 bg-color-segundary hover:bg-color-primary  text-white  rounded-lg">
                                                        <IconEdit />
                                                    </div>
                                                </Link>
                                            </div>
                                        </td>)
                                    }
                                    { onDelete && (
                                        <td className='w-2 '>
                                            <IconButton onClick={ (env) => HandleDelete(row) }>  <IconTrash /> </IconButton>
                                        </td>)
                                    }
                                    { menuOption && (
                                        <td className='w-2 '>
                                            <IconButtonOptions menuOption={ menuOption } item={ row }>   </IconButtonOptions>
                                        </td>)
                                    }

                                </tr>

                            )
                        }) }
                </tbody>
            </table>
        </div>
    </>);
}
TableInfinite.protoTypes = {
    autonumeric: PropTypes.bool,
    indexColumnsHidden: PropTypes.array,
    columns: PropTypes.array,
    rows: PropTypes.array,
    onDelete: PropTypes.func,
    onEditLink: PropTypes.string,
    markItems: PropTypes.array,
    menuOption: PropTypes.shape({
        options: PropTypes.arrayOf({
            icon: PropTypes.Any,
            label: PropTypes.label,
            onClick: PropTypes.func
        }),
        icon: PropTypes.Any,
        label: PropTypes.label
    })
}

TableInfinite.defaultProps = {
    autonumeric: true,
    indexColumnsHidden: [],
    columns: [],
    rows: [],
    onDelete: null,
    onEditLink: '',
    onSelect: null,
    markItems: ['1'],
    menuOption: null
}
export default TableInfinite