import { createContext, useEffect } from 'react'
import { useState } from "react"
import { KEY_CACHE_COMPANY, KEY_CACHE_USER, KEY_SESSION_USER } from '../constants'
import { fakeCache } from '../data/fakeCache'


// Este es el que tenemos que consumir
export const AppContext = createContext()

// Este es el que nos provee de acceso al contexto
export function ApplicationProvider ({ children }) {

    const [company, setCompany] = useState()
    const [isLoading, setIsLoading] = useState()
    const [user, setUser] = useState(null)
    const [navegatorSelectted, setNavegatorSelectted] = useState(null)

    const logout = async () => {

        setUser(null);
        setCompany(null)
        fakeCache.clear();

        window.location.href = "/login"

    };
    const login = (user) => {
        if (user) {
            const userSession = { name: user.name, email: user.email, id: user.id }
            const company = { ...user.company }
            fakeCache.setItem(KEY_CACHE_USER, userSession);
            sessionStorage.setItem(KEY_SESSION_USER, user.tokenSession);
            fakeCache.setItem(KEY_CACHE_COMPANY, company);
            setUser(userSession)
            setCompany(company)
        } else {
            setUser(null)
            setCompany(null)
            fakeCache.clear()
        }
        //navigate ("/")
    };
    useEffect(() => {

        const user = fakeCache.getItem(KEY_CACHE_USER)
        setUser(fakeCache.getItem(KEY_CACHE_USER))
        setCompany(fakeCache.getItem(KEY_CACHE_COMPANY))
    }, []);
    return (
        <AppContext.Provider value={ { company, user, navegatorSelectted, login, logout, setNavegatorSelectted, isLoading, setIsLoading } }
        >
            { children }
        </AppContext.Provider>
    )
}