import PropTypes from 'prop-types';
import uuid from 'react-uuid';

const ToolBar = ({ className, buttons, optionSelected }) => {
    return (
        <div className={ "bg-slate-900 " + className }>
            <ul className=' list-none  flex justify-center' >
                {
                    buttons.map(({ id, icon, active, onclick }) => {
                        const selected = id == optionSelected ? 'bg-sky-700' : ''
                        return (
                            <li className={ 'px-5 py-2  text-gray-300 block text-center float-right hover:bg-sky-700 hover:cursor-pointer ' + selected } key={ uuid() }>
                                <div className={ 'text-slate-300 ' } onClick={ onclick } >
                                    <div >{ icon }</div>
                                    <div>{ id }</div>

                                </div>
                            </li>)
                    })
                }
            </ul >
        </div>
    )
}
ToolBar.protoTypes = {
    buttons: PropTypes.arrayOf({
        id: PropTypes.string,
        icon: PropTypes.any,
        active: PropTypes.bool,
        onclick: PropTypes.func
    }),
    optionSelected: PropTypes.string

}
ToolBar.defaultProps = {
    buttons: [],
    optionSelected: ""

}
export default ToolBar