import { useContext, useEffect, useState } from "react";


import { AddSalesDetails, DeleteSalesDetails, GetCurrentNoteSales, NewNoteSales, SaveNoteSales, SetSalesClient } from "../services/sales"
import TableProductsSales from "./TableProductsSales";
import TableClientSales from "./TableClientSales";
import FieldSearch from "../components/FieldSearch";
import ModalPopup from "../components/ModalPopup";
import TableDetailsSales from "./TableDetailsSales";
import { IconPrint, IconSearch } from "../components/Icons";
import IconButton from "../components/IconButton";
import Button from "../components/Button";
import { GetRptNoteSalesById } from "../services/reports/sales";

import { AppContext } from "../context/applicationProvider";


/*********************FORM *********************/
const FormSales = () => {
    const [noteSales, setNoteSales] = useState({
        id: null,
        client: null,
        salesdetails: [],
        total: 0
    })
    const [lastUpdated, setLastUpdated] = useState(null)
    const [openListClients, setOpenListClients] = useState(false);
    const handleOpenListClients = () => { console.log("tests"); setOpenListClients(true); }
    const handleCloseListClients = () => setOpenListClients(false);
    const [openListProds, setOpenListProds] = useState(false);
    const handleOpenListProds = () => setOpenListProds(true);
    const handleCloseListProds = () => setOpenListProds(false);
    let appContext = useContext(AppContext)
    const columnsDetails = ["Codigo", "descripcion", "precio", "cantidad", "subTotal"]
    const indexColumnsHidden = [0]

    const HandleOnSubmitForm = async (env) => {
        appContext.setIsLoading(true)
        const note = await SaveNoteSales()
        const noteUrl = await GetRptNoteSalesById(note.id)
        appContext.setIsLoading(false)
        window.open(noteUrl, '_blank');
        window.location.reload();
    }
    const handleDeleted = async (element) => {
        appContext.setIsLoading(true)
        const itemDeleted = await DeleteSalesDetails(element.id)
        const index = noteSales.salesdetails.findIndex(x => itemDeleted.prodId == x.prodId)

        if (index >= 0) {

            noteSales.salesdetails.splice(index, 1)
        }

        setNoteSales({ ...noteSales })
        appContext.setIsLoading(false)

    }

    const HandleSelectClient = async (clItem) => {

        if (noteSales.id == null) {
            const newNote = await NewNoteSales(clItem.id)
            setNoteSales(newNote)
        } else {
            const client = await SetSalesClient(clItem.id)
            setNoteSales({ ...noteSales, client })
        }

        setOpenListClients(false)
        console.log(clItem)
    }
    const HandleSelectProduct = async (prodItem) => {
        await onKeyEnter(prodItem.id)

    }

    const GetData = async () => {
        appContext.setIsLoading(true)
        const note = await GetCurrentNoteSales()
        if (note.id == null) {
            setOpenListClients(true)
        } else {
            setNoteSales(note)
        }
        appContext.setIsLoading(false)
    }
    const onKeyEnter = async (value) => {
        appContext.setIsLoading(true)
        const itemSave = await AddSalesDetails(value, 1)
        setNoteSales(itemSave)
        setOpenListProds(false)
        appContext.setIsLoading(false)
    }
    useEffect(() => {
        GetData()
    }, [])
    return (<>
        <div className="m-5" >
            <div className='note-sales'>
                <h1> Ventas</h1>
                <div className="flex border-2 my-3 px-3 py-2 uppercase">
                    <div>
                        <div><span className="font-semibold" >Cliente : </span> <span>{ noteSales?.client?.id } </span><br />
                            <span className="font-semibold "> { noteSales?.client?.firstName } { noteSales?.client?.lastName }</span>
                        </div>
                    </div>
                    <div className=" pl-2">
                        <IconButton onClick={ handleOpenListClients }>
                            <IconSearch /> <span className="mr-2">Buscar</span>
                        </IconButton>
                    </div>
                </div>
                { noteSales.id && (
                    <div >
                        <div />
                        <div className="pl-2 border-2 p-5 px-3  uppercase"  >
                            <span className="font-semibold mb-2 block " > ingrese el producto a Vender:</ span>
                            <FieldSearch label={ "codigo " } onKeyEnter={ onKeyEnter } onClickIconButton={ handleOpenListProds }></FieldSearch>

                        </div>
                        <div />
                        <div className=" mt-4 border-2">
                            <TableDetailsSales
                                indexColumnsHidden={ indexColumnsHidden }
                                datas={ noteSales.salesdetails }
                                desc={ noteSales.descuento }
                                columns={ columnsDetails }
                                total={ noteSales?.total }
                                onDelete={ handleDeleted }
                                lastUpdated={ lastUpdated }>
                            </TableDetailsSales>
                        </div>
                        <div className="flex justify-center mt-5">
                            <Button primary onClick={ HandleOnSubmitForm }><IconPrint />Guardar</Button>
                        </div>
                    </div>
                ) }
            </div>
            <div className='list-object'>

                <div className='list-Products'>
                    <ModalPopup
                        fullScreen
                        open={ openListProds }
                        title={ "Lista de Productos" }
                        onClose={ handleCloseListProds }
                    >
                        <TableProductsSales onSelect={ HandleSelectProduct }></TableProductsSales>
                    </ModalPopup>
                </div>
                <div className='list-Clients'>
                    <ModalPopup
                        open={ openListClients }
                        title={ "Lista de Clientes" }
                        onClose={ handleCloseListClients }
                    >
                        <TableClientSales onSelect={ HandleSelectClient } ></TableClientSales>
                    </ModalPopup>
                </div>
            </div>
        </div >
    </>
    )
}
export default FormSales
