import { IconClose } from "./Icons";
import ReactModal from "react-modal";


export default function ModalPopup ({ open, title, children, onClose }) {
    ReactModal.setAppElement('*')
    return (
        <ReactModal
            isOpen={ open }
            title={ title }
        >
            <div className=" bg-color-primary text-white" >
                <div className=" flex justify-start content-start">
                    <div className=" hover:cursor-pointer"
                        onClick={ onClose } >
                        <IconClose />
                    </div>
                    <div className="uppercase flex justify-center items-center font-semibold w-full text-center">
                        { title }
                    </div>

                </div>
            </div>
            { children }
        </ReactModal >
    )
}