import uuid from "react-uuid"
const SelectBox = ({ options, onChange }) => {
    const handleOnChange = (env) => {
        console.log(env.target.value)
        onChange(env.target.value)
    }
    return (<div className=" flex justify-start flex-col w-3/4 ">
        <label htmlFor="cars">Company</label> <br />
        <select onChange={ handleOnChange } className="relative w-full cursor-default rounded-md bg-white py-1.5 pl-3 pr-10 text-left text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:outline-none focus:ring-2 focus:ring-indigo-500 sm:text-sm sm:leading-6">
            {
                options.map(item => (
                    <option key={ uuid() } value={ item.id }>{ item.label }</option>
                )
                )
            }
        </select>
    </div>)

}

export default SelectBox