//import { useNavigate } from "react-router-dom"
import {  useState, useContext, useRef } from "react";
import { SignIn } from "../services/session"
import { AppContext } from '../context/applicationProvider';
import TextField from "../components/TextField";
import Button from "../components/Button";
import useInput from "../hook/useInput";
import { IconUser } from "../components/Icons";

const Login = () => {
    const userName = useInput("milton", 'userName', () => { setMessageError(null) })
    const password = useInput("milton", 'password', () => { setMessageError(null) })
    const [messageError, setMessageError] = useState(null)
    const session = useContext(AppContext);

    const handleOnLogin = async (env) => {
        const user = await SignIn(userName.props.value, password.props.value)
        if (user) {
            session.login(user)
        } else {
            setMessageError("Error User and Password invalido")
        }
    }
    return (
        <>
            <div className="center" >
                <TextField
                    { ...userName.props }
                    name="userName"
                ><IconUser /></TextField>
                <TextField
                    { ...password.props }
                    type="password"
                    name="password"
                ><IconUser /></TextField>

                <Button onClick={ handleOnLogin }>Login</Button>
                <span > { messageError }</span>
            </div>
        </>
    )
}
export default Login