import { useContext, useEffect, useState } from "react"
import PropTypes from 'prop-types';
import { DeleteClient, GetClients } from "../services/clients"
import { ROUTES } from "../constants"
import Table from './Table'
import { IconMenu } from "../components/Icons";

import { AppContext } from "../context/applicationProvider";


const TableClients = ({ onselect, readOnly }) => {
    const [listClients, setListClients] = useState([])
    let appContext = useContext(AppContext)

    async function getDatas (filter = '') {
        appContext.setIsLoading(true)
        setListClients(await GetClients(filter))
        appContext.setIsLoading(false)

    }
    const HandleOnDelete = async (client) => {
        const itemDeleted = await DeleteClient(client.id)
        const indexof = listClients.indexOf(x => x.id === client.id)
        if (indexof !== -1) {
            listClients.splice(itemDeleted, 1)
        }

    }
    const onchange = async (value) => {
        if (value.length === 0) {

            getDatas()
        } else {
            const filter = value
            getDatas(filter)
        }
    }
    useEffect(() => {
        getDatas()
    }, [])
    const columns = ["Nombre", "Apellido", "email", "Telefono", "Deuda"]
    const indexColumnsHidden = [0]
    const actions = {
        onDelete: HandleOnDelete,
        linkToNew: ROUTES.CLIENTS_NEW,
        linkToEdit: ROUTES.CLIENTS_EDIT
    }
    const menuOption = {
        options: [
            {
                label: "Pagar Notas",
                onClick: (itemSelected) => window.location.href = ROUTES.PAYMENTS + "/" + itemSelected.id
            },
            {
                label: "Asignar Activos",
                onClick: (itemSelected) => {

                    window.location.href = ROUTES.ACTIVES + "/" + itemSelected.id

                }
            }
        ],
        icon: (<IconMenu></IconMenu>),
        label: ''

    }
    return (<>

        <div className="w-full">
            < Table indexColumnsHidden={ indexColumnsHidden } datas={ listClients } columns={ columns } onChangeSearch={ onchange } actions={ actions } menuOption={ menuOption } />
        </div >
    </>
    )
}
TableClients.protoTypes = {
    onSelect: PropTypes.func,
    readOnly: PropTypes.bool
}
TableClients.defaultProps = {
    onSelect: null,
    readOnly: false
}

export default TableClients