
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_USERS = URL_SERVER + "/users"

export const GetUsers = async (filter) => {
    try {
        let url = URL_SERVER_USERS
        if (filter != '') {
            const params = `?userName=${ filter }&&email=${ filter }`
            url = URL_SERVER_USERS + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetUserById = async (id = 0) => {
    try {
        let url = URL_SERVER_USERS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateUser = async ({ id, userName, email }) => {
    try {
        let url = URL_SERVER_USERS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { userName, email })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreateUser = async ({ userName, email, company }) => {
    try {
        let url = URL_SERVER_USERS
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { userName, email, company })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeleteUser = async (id) => {
    try {
        let url = URL_SERVER_USERS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}