import * as React from 'react';
import PropTypes from 'prop-types';
import { AppContext } from '../context/applicationProvider';

import IconButton from '../components/IconButton';
import { IconActivos, IconAvatar, IconClients, IconCompanys, IconEmployees, IconPayments, IconProducts, IconProvider, IconPurchases, IconReports, IconSales, IconUsers } from '../components/Icons';
import ToolBar from '../components/Toolbar';
import { LABELS, ROUTES } from '../constants';
import { useNavigate } from 'react-router-dom';

function Header ({ title }) {

    const [optionSelected, SetOptionSelected] = React.useState()
    let appContext = React.useContext(AppContext)
    const navigate = useNavigate()
    const categories = [
        {
            id: LABELS.REPORTE,
            icon: <IconReports />,
            onclick: () => {
                SetOptionSelected(LABELS.REPORTE)

                navigate(ROUTES.REPORTS)
            }
        },
        {
            id: LABELS.COMPANY,
            icon: <IconCompanys />,
            onclick: () => {
                SetOptionSelected(LABELS.COMPANY)
                navigate(ROUTES.COMPANY)
            }
        },
        {
            id: LABELS.PAYMENTS,
            icon: <IconPayments />,
            onclick: () => {
                SetOptionSelected(LABELS.PAYMENTS)
                navigate(ROUTES.PAYMENTS)
            }
        },
        {
            id: LABELS.NOTE_SALES,
            icon: <IconSales />,
            onclick: () => {
                SetOptionSelected(LABELS.NOTE_SALES)
                navigate(ROUTES.NOTE_SALES)
            }
        },
        {
            id: LABELS.PURCHASES,
            icon: <IconPurchases />,
            onclick: () => {
                SetOptionSelected(LABELS.PURCHASES)

                navigate(ROUTES.PURCHASES)
            }
        }, {
            id: LABELS.ACTIVE,
            icon: <IconActivos />,
            onclick: () => {
                SetOptionSelected(LABELS.ACTIVE)

                navigate(ROUTES.ACTIVES)
            }
        },
        {
            id: LABELS.EMPLOYEES,
            icon: <IconEmployees />,
            onclick: () => {
                SetOptionSelected(LABELS.EMPLOYEES)
                navigate(ROUTES.EMPLOYEES)
            }
        },
        {
            id: LABELS.PRODUCTS,
            icon: <IconProducts />,

            onclick: () => {
                SetOptionSelected(LABELS.PRODUCTS)
                navigate(ROUTES.PRODUCTS)
            }
        },
        {
            id: LABELS.PROVIDERS,
            icon: <IconProvider />,

            onclick: () => {
                SetOptionSelected(LABELS.PROVIDERS)
                navigate(ROUTES.PROVIDERS)
            }
        },
        {
            id: LABELS.CLIENTS,
            icon: <IconClients />,

            onclick: () => {
                SetOptionSelected(LABELS.CLIENTS)
                navigate(ROUTES.CLIENTS)
            }
        },
        {
            id: LABELS.USERS,
            icon: <IconUsers />,
            onclick: () => {
                SetOptionSelected(LABELS.USERS)
                navigate(ROUTES.USERS)
            }
        },


    ];
    return (
        <div className='w-full'>
            <div className='flex justify-between'>
                <div className=''>
                    <img className='w-5/12' src="/logo.svg" alt="sisccovem" />

                    <span className='text-3xl'>{ appContext.company?.name }</span>
                </div>
                <div >
                    <div className='flex justify-between' >
                        <span className='mr-2 flex items-center font-semibold font-serif'> { appContext.user?.name }</span>
                        <IconButton className="fill-white" onClick={ () => appContext.logout() }>
                            <IconAvatar />

                        </IconButton>
                    </div>

                </div>
            </div>
            <ToolBar optionSelected={ optionSelected } buttons={ categories }></ToolBar>

        </div>

    );
}

Header.propTypes = {

    title: PropTypes.string,
    categories: PropTypes.array
};
Header.defaultProps = {
    title: "",
    categories: []
}


export default Header;