const IconButton = ({ children, onClick, ...prop }) => {
    const handleOnClieck = () => {

        if (onClick) {
            onClick()

        }
    }
    return (<div
        { ...prop }
        className=" mx-2 hover:border-color-primary capitalize " 
        onClick={ handleOnClieck }
    >
        <div className="hover:cursor-pointer p-1  bg-color-segundary hover:bg-color-primary  text-white  rounded-lg  flex justify-center items-center flex-row">
            { children }
        </div>
    </div >)
}
export default IconButton