import { URL_SERVER_PDF } from "../../constants.js"
import { handlerError } from "../handlerError.js"
const URL_SERVER = URL_SERVER_PDF + "/reports/sales"

export const GetRptNoteSalesById = (id) => {
    try {
        let url = URL_SERVER + "/NoteSales/" + id
        return url
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}