
import { IconReportUsers, } from "../../components/Icons";
import Link from "../../components/Link";
import { GetReportsUsers } from "../../services/reports/users";

const ReportUsers = () => {
    const linkUsersList = () => {
        return GetReportsUsers()
    }
    return <div className="flex flex-wrap  content-around">
        <Link href={ linkUsersList() } label="lista de Usuarios"><IconReportUsers /></Link>
    </div>
}
export default ReportUsers