import { useContext, useEffect, useState } from "react";
import useInput from "../hook/useInput";
import { CreateProvider, GetProviderById, UpdateProvider } from "../services/providers";
import TextField from "../components/TextField";
import { IconUser } from "../components/Icons";
import Button from "../components/Button";

import { useNavigate } from "react-router-dom";
import { AppContext } from "../context/applicationProvider";

/*********************FORM *********************/
const FormSupplier = ({ providerId }) => {
    let appContext = useContext(AppContext)
    const id = useInput("", 'id')
    const name = useInput("", 'Nombre')
    const phone = useInput("", 'Telefono')
    const address = useInput("", 'Direccion')
    const imagen = useInput("", 'Imagen')
    const navegate = useNavigate()
    const HandleOnSubmitForm = async (env) => {
        appContext.setIsLoading(true)
        if (providerId) {
            await UpdateProvider({ id: id.props.value, name: name.props.value, phone: phone.props.value, address: address.props.value, imagen: imagen.props.value })

        } else {
            await CreateProvider({ id: id.props.value, name: name.props.value, phone: phone.props.value, address: address.props.value, imagen: imagen.props.value })
        }
        appContext.setIsLoading(false)

        navegate("/")
    }
    async function getData (providerId) {
        appContext.setIsLoading(true)
        const provider = await GetProviderById(providerId)
        id.setValue(provider.id)
        name.setValue(provider.name)
        phone.setValue(provider.phone)
        address.setValue(provider.address)
        imagen.setValue(provider.imagen)
        appContext.setIsLoading(false)

    }
    useEffect(() => {
        if (providerId) {
            getData(providerId)
        }
    }, [])



    return (<>

        <div >
            <h1 className="mb-5">Formulario Proveedores</h1>
            <div className='center border-2 border-color-segundary'  >
                <TextField
                    { ...name.props }
                    name="name"
                >  <IconUser /></TextField>
                <TextField
                    { ...phone.props }
                    name="phone"
                >  <IconUser /></TextField>
                <TextField
                    { ...address.props }
                    name="address"
                >  <IconUser /></TextField>
            </div>
            <div className="flex justify-center border-2 p-5 border-color-segundary">
                <Button primary disabled={ name.error || phone.error || address.error }
                    onClick={ HandleOnSubmitForm }>Guardar</Button>

            </div>
        </div >
    </>
    )
}
export default FormSupplier