
import { IconReportEmployees, } from "../../components/Icons";
import Link from "../../components/Link";
import { GetReportsEmployees } from "../../services/reports/employees";

const ReportEmployees = () => {
    const linkEmployeesList = () => {
        return GetReportsEmployees()
    }
    return <div className="flex flex-wrap  content-around">
        <Link href={ linkEmployeesList() } label="lista de Empleados"><IconReportEmployees /></Link>
    </div>
}
export default ReportEmployees