
import { useEffect, useState } from "react"
import Button from "./Button"
import ReactModal from "react-modal"

export default function ModalConfirmation ({ show, onOkClick = () => {}, onCancelClick = () => {}, messageText, title }) {
    ReactModal.setAppElement('*')
    const [open, setOpen] = useState(show)
    const handleCloseOnclick = () => {
        onCancelClick()
        setOpen(false)
    }
    const handleOkOnclick = () => {
        onOkClick()
        setOpen(false)
    }
    useEffect(() => {

        setOpen(show)
    }, [show])
    return (<div className="bg-black">
        <ReactModal
            isOpen={ open }
            onRequestClose={ handleCloseOnclick }
            contentLabel="Example Modal"
            className="w-80 border-2 m-auto mt-48 bg-white"
            overlayClassName="Overlay"
        >
            <h2 className="p-3 bg-color-segundary text-white">
                { title }
            </h2 >
            <div className="center">

                <div >

                    { messageText }

                </div>
                <div >
                    <Button className="inline-block" onClick={ handleCloseOnclick }>Cancelar</Button>
                    <Button primary className="inline-block" onClick={ handleOkOnclick } autoFocus>  Aceptar  </Button>
                </div>
            </div>
        </ReactModal>

    </div>
    )
}