import { useState } from "react"
import ToolBar from "../../components/Toolbar"
import { LABELS } from "../../constants"
import ReportClients from "./reportClient"
import ReportProducts from "./reportProducts"
import ReportActives from "./reportActives"
import ReportSales from "./reportSales"
import ReportEmployees from "./reportEmployees"
import ReportUsers from "./reportUsers"
import ReportProviders from "./reportProviders"


function ReportsPage () {
    const [optionSelected, SetOptionSelected] = useState(LABELS.CLIENTS)

    const buttons = [
        {
            id: LABELS.CLIENTS,
            icon: null,
            active: optionSelected == LABELS.CLIENTS,
            onclick: () => {
                SetOptionSelected(LABELS.CLIENTS)
            }
        },
        {
            id: LABELS.PRODUCTS,
            icon: null,
            active: false,
            onclick: () => {
                SetOptionSelected(LABELS.PRODUCTS)
            }
        },
        {
            id: LABELS.PROVIDERS,
            icon: null,
            active: false,
            onclick: () => {
                SetOptionSelected(LABELS.PROVIDERS)
            }
        },
        {
            id: LABELS.USERS,
            icon: null,
            active: false,
            onclick: () => {
                SetOptionSelected(LABELS.USERS)
            }
        },
        {
            id: LABELS.EMPLOYEES,
            icon: null,
            active: false,
            onclick: () => {
                SetOptionSelected(LABELS.EMPLOYEES)
            }
        },
        {
            id: LABELS.SALES,
            icon: null,
            active: false,
            onclick: () => {
                SetOptionSelected(LABELS.SALES)
            }
        }, {
            id: LABELS.ACTIVE,
            icon: null,
            active: false,
            onclick: () => {
                SetOptionSelected(LABELS.ACTIVE)
            }
        },
    ]

    const renderOption = (option) => {
        switch (option) {
            case LABELS.CLIENTS: return (<ReportClients></ReportClients>)
            case LABELS.PRODUCTS: return (<ReportProducts> </ReportProducts>)
            case LABELS.PROVIDERS: return (<ReportProviders> </ReportProviders>)
            case LABELS.USERS: return (<ReportUsers> </ReportUsers>)
            case LABELS.EMPLOYEES: return (<ReportEmployees></ReportEmployees>)
            case LABELS.SALES: return (<ReportSales></ReportSales>)
            case LABELS.ACTIVE: return (<ReportActives> </ReportActives>)
            default: return (<ReportClients></ReportClients>)


        }
    }

    return (<div className="flex justify-start flex-col content-start h-screen w-full mx-5 ">

        <ToolBar className="bg-slate-800 " buttons={ buttons } optionSelected={ optionSelected } />
        <div className="uppercase mt-5">
            <h1 className="font-bold text-center">Reportes { optionSelected }</h1>
            <div className=" border-2  min-h-fit w-full p-2">
                { renderOption(optionSelected) }
            </div>
        </div>
    </div>


    )
}

export default ReportsPage