
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_COMPANYS = URL_SERVER + "/companys"

export const GetCompanys = async (filter = '') => {
    try {
        let url = URL_SERVER_COMPANYS
        if (filter !== '') {
            const params = `?name=${ filter }&&phone=${ filter }&&address=${ filter }`
            url = URL_SERVER_COMPANYS + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetCompanyById = async (id = 0) => {
    try {
        let url = URL_SERVER_COMPANYS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateCompany = async ({ id, name, phone, address }) => {
    try {
        let url = URL_SERVER_COMPANYS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { id, name, phone, address })

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreateCompany = async ({ name, phone, address }) => {
    try {
        let url = URL_SERVER_COMPANYS
        const response = await axios.post(url, { name, phone, address })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeleteCompany = async (id) => {
    try {
        let url = URL_SERVER_COMPANYS + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}