import { useState } from "react";
const useInput = (initialValue, label, onChangeValue = (value) => {}, onBlur = (value) => {}) => {
    const [value, setValue] = useState(initialValue);
    const [errorMessage, setErrorMessage] = useState(initialValue);
    const [error, setError] = useState(false);


    const handleOnBlur = async (event) => {
        if (value === "") {
            setErrorMessage(label + " es requerido")
            setError(true)


        } else {

            await onBlur(value)
            setError(false)
            setErrorMessage("")
        }
    }
    const handleChange = (value) => {

        setValue(value);
        setError(false)
        onChangeValue(value)
        setErrorMessage("")
    };

    return {

        setValue,
        props: {
            value,
            onchange: handleChange,
            onBlur: handleOnBlur,
            label,
            error: error,
            helperText: errorMessage
        }
    };
};

export default useInput
