
import { KEY_SESSION_USER, URL_SERVER } from "../constants.js"
import axios from 'axios'
import { handlerError } from "./handlerError.js"
const URL_SERVER_EMPLOYEES = URL_SERVER + "/Employees"

export const GetEmployees = async (filter) => {
    try {
        let url = URL_SERVER_EMPLOYEES
        if (filter != '') {
            const params = `?firstName=${ filter }&&lastName=${ filter }&&email=${ filter }&&phone=${ filter }`
            url = URL_SERVER_EMPLOYEES + params
        }
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const GetEmployeeById = async (id = 0) => {
    try {
        let url = URL_SERVER_EMPLOYEES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.get(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const UpdateEmployee = async ({ id, firstName, lastName, email, phone, salary }) => {
    try {
        let url = URL_SERVER_EMPLOYEES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.put(url, { firstName, lastName, email, phone, salary })

        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const CreateEmployee = async ({ firstName, lastName, email, phone, salary }) => {
    try {
        let url = URL_SERVER_EMPLOYEES
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.post(url, { firstName, lastName, email, phone, salary })
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}
export const DeleteEmployee = async (id) => {
    try {
        let url = URL_SERVER_EMPLOYEES + '/' + id
        axios.defaults.headers.Authorization = sessionStorage.getItem(KEY_SESSION_USER)
        const response = await axios.delete(url)
        return response.data
    }
    catch (error) {
        handlerError(error); console.log(error)
    }
}