import { useEffect, useState } from "react"

export default function Button ({ primary = false, children, onClick = () => {}, ...props }) {
    const [isPrimary, setIsPrimary] = useState("bg-white  text-gray-5000 hover:text-gray-50 hover:bg-gray-100 ")

    useEffect(() => {
        console.log(primary)
        if (primary) {
            setIsPrimary("bg-color-primary text-gray-50")
        }
    }, [primary])
    return (
        <div { ...props }>
            <button onClick={ onClick }
                className={ "  hover:bg-gray-500  border border-gray-200 focus:ring-4 focus:outline-none  focus:ring-gray-100 font-medium rounded-lg text-sm px-5 py-2.5 text-center inline-flex items-center   justify-center " + isPrimary } >

                { children }
            </button></div>)


}